import { useParams, useLocation } from 'react-router-dom';
// material
import { Box, Container, Typography } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import BonusNewForm from '../components/bonus/BonusNewForm';
import BonusWinnersTable from '../components/bonus/BonusWinnersTable';
import { mockImgProduct } from '../utils/mockImages';
import BonusSetListTable from '../components/bonus/BonusSetListTable';

// ----------------------------------------------------------------------

export default function BonusCreate() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const { name } = useParams();
  const isEdit = pathname.includes('edit');

  // TODO: will be updated with bonus
  const currentBonus = isEdit
    ? {
        images: [mockImgProduct(1), mockImgProduct(1)],
        thumbnail: mockImgProduct(1),
        name: '100 shoes',
        description: '100 shoes for all.',
        giveawayType: 'Level3',
        status: true,
        startDate: new Date(),
        value: 1500,
        availableQuantity: 100,
      }
    : {};

  return (
    <Page title="Bonus: Create a new bonus">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Add Bonus' : 'Edit Bonus'}
          links={[{ name: 'Bonus', href: PATH_DASHBOARD.root }, { name: !isEdit ? 'Add Bonus' : name }]}
        />

        <BonusNewForm isEdit={isEdit} currentBonus={currentBonus} />

        {isEdit && (
          <Box sx={{ maxHeight: 720, mt: (theme) => theme.spacing(8) }}>
            <Typography sx={{ mb: 2 }} variant="h5">
              Bonus Set List
            </Typography>
            <BonusSetListTable
              bonusList={[
                {
                  name: 'Set 1',
                  brand: 'Nike',
                  bonuses: ['Bonus 1', 'Bonus 2', 'Bonus 3'],
                  endDate: new Date().toDateString(),
                  ticketRules: ['Default Historical Data', 'Default Followers'],
                  averageValueOfSingleBonus: 200,
                  valueOfAllBonuses: 2000,
                  winnerNumber: 10,
                  bonusTasksCompleted: 60,
                  numberOfParticipants: 200,
                  numberOfTickets: 2000,
                  averageNumberOfUserTickets: 150,
                },
                {
                  name: 'Set 2',
                  brand: 'Nike',
                  bonuses: ['Bonus 2', 'Bonus 4'],
                  endDate: new Date().toDateString(),
                  ticketRules: ['Default Historical Data', 'Default Followers'],
                  averageValueOfSingleBonus: 500,
                  valueOfAllBonuses: 3000,
                  winnerNumber: 5,
                  bonusTasksCompleted: 40,
                  numberOfParticipants: 100,
                  numberOfTickets: 500,
                  averageNumberOfUserTickets: 15,
                },
                {
                  name: 'Set 3',
                  brand: 'Nike',
                  bonuses: ['Bonus 2', 'Bonus 12', 'Bonus 21', 'Bonus 33'],
                  endDate: new Date().toDateString(),
                  ticketRules: ['Default Historical Data', 'Default Followers'],
                  averageValueOfSingleBonus: 350,
                  valueOfAllBonuses: 2500,
                  winnerNumber: 20,
                  bonusTasksCompleted: 35,
                  numberOfParticipants: 250,
                  numberOfTickets: 900,
                  averageNumberOfUserTickets: 45,
                },
              ]}
            />
          </Box>
        )}

        {isEdit && <BonusWinnersTable />}
      </Container>
    </Page>
  );
}
