import PropTypes from 'prop-types';

import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';

import Scrollbar from '../Scrollbar';

const TABLE_HEAD = [
  { id: 'name', label: 'Name', alignRight: false },
  { id: 'brand', label: 'Brand', alignRight: false },
  { id: 'endDate', label: 'End Date', alignRight: false },
  { id: 'numberOfBonuses', label: 'Number of bonuses', alignRight: false },
  { id: 'averageValueOfSingleBonus', label: 'Average $ of a single bonus', alignRight: false },
  { id: 'valueOfAllBonuses', label: 'Value of all bonuses', alignRight: false },
  { id: 'winnerNumber', label: 'Winner Number', alignRight: false },
  { id: 'bonusTasksCompleted', label: 'Bonus Tasks Completed (%)', alignRight: false },
  { id: 'numberOfParticipants', label: '# of participants', alignRight: false },
  { id: 'numberOfTickets', label: '# of tickets', alignRight: false },
  { id: 'averageNumberOfUserTickets', label: 'Average # of user tickets', alignRight: false },
];

BonusSetListTable.propTypes = {
  bonusList: PropTypes.array,
};

function BonusSetListTable({ bonusList = [] }) {
  return (
    <Scrollbar>
      <TableContainer sx={{ minWidth: 800 }}>
        <Table aria-label="bonus table">
          <TableHead>
            <TableRow>
              {TABLE_HEAD.map((headCell) => (
                <TableCell key={headCell.id} align={headCell.alignRight ? 'right' : 'left'}>
                  {headCell.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {bonusList.map((row) => (
              <TableRow key={row.name}>
                <TableCell>{row.name}</TableCell>
                <TableCell>{row.brand}</TableCell>
                <TableCell>{row.endDate}</TableCell>
                <TableCell>{row.bonuses.length}</TableCell>
                <TableCell>{row.averageValueOfSingleBonus}</TableCell>
                <TableCell>{row.valueOfAllBonuses}</TableCell>
                <TableCell>{row.winnerNumber}</TableCell>
                <TableCell>{row.bonusTasksCompleted}</TableCell>
                <TableCell>{row.numberOfParticipants}</TableCell>
                <TableCell>{row.numberOfTickets}</TableCell>
                <TableCell>{row.averageNumberOfUserTickets}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Scrollbar>
  );
}

export default BonusSetListTable;
